import * as React from 'react';
import Button from '@mui/material/Button';

import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';

export interface AboutDialogProps {
  open: boolean;
  onClose: (value?: string) => void;
}

export const AboutDialog = (props: AboutDialogProps) => {
  const { onClose, open } = props;

  const handleClose = () => {
    onClose();
  };

  return (
    <Dialog onClose={handleClose} open={open}>
      <DialogTitle>WODdrive will back online soon!</DialogTitle>
      <DialogContent>
        <DialogContentText id='wod-description'>
          The WODrive has undergone a few iterations over the last decade or so
          but the goal of this project has always been the same - to provide an
          extensive community-driven resource for workouts and fitness
          challenges. The WODdrive gives you instant access to tens of thousands
          of workouts involving everything from powerlifting and Olympic
          weightlifting to cross-training, bodyweight conditioning, and
          endurance training. There is something for everyone and for every
          environment. Get your <u>W</u>orkout <u>o</u>f the <u>D</u>ay anytime,
          anywhere, and on any device.
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} autoFocus>
          Close
        </Button>
      </DialogActions>
    </Dialog>
  );
};
