import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import { IRxdWorkout } from './api';

import {
  Alert,
  Fab,
  Skeleton,
  Toolbar,
  Typography,
} from '@mui/material';
import Timeline from '@mui/lab/Timeline';
import TimelineItem, { timelineItemClasses } from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineDot from '@mui/lab/TimelineDot';
import RefreshIcon from '@mui/icons-material/RefreshOutlined';

export default function Workout({
  data,
  format,
  refetch,
  isFetching,
  error,
}: {
  data?: IRxdWorkout;
  format?: string;
  refetch: () => void;
  isFetching: boolean;
  error: any;
}) {
  return (
    <Card elevation={0} sx={{ width: '100%' }}>
      <CardContent>
        {error && (
          <Alert severity='error'>
            Our service is currently unavailable =(
          </Alert>
        )}
        {isFetching || error ? (
          <React.Fragment>
            <Timeline
              sx={{
                [`& .${timelineItemClasses.root}:before`]: {
                  flex: 0,
                  padding: 0,
                },
              }}
              nonce={undefined}
              onResize={undefined}
              onResizeCapture={undefined}
            >
              <React.Fragment>
                {[1, 2, 3, 4].map((line, index) => (
                  <TimelineItem key={index}>
                    <TimelineSeparator>
                      <TimelineDot variant='outlined' />
                      <TimelineConnector />
                    </TimelineSeparator>
                    <TimelineContent>
                      <Skeleton
                        animation='wave'
                        height={18}
                        width={Math.floor(Math.random() * 100) + 1 + '%'}
                      />
                    </TimelineContent>
                  </TimelineItem>
                ))}
              </React.Fragment>
            </Timeline>
          </React.Fragment>
        ) : (
          <>
            {data && data?.commands[0] && (
              <>
                <Typography variant='h4'>{data.commands[0]}</Typography>
              </>
            )}
            <Timeline
              sx={{
                [`& .${timelineItemClasses.root}:before`]: {
                  flex: 0,
                  padding: 0,
                },
              }}
              nonce={undefined}
              onResize={undefined}
              onResizeCapture={undefined}
            >
              <React.Fragment>
                {(data?.commands.slice(1) || []).map((line, index) => (
                  <TimelineItem key={index}>
                    <TimelineSeparator>
                      <TimelineDot variant='outlined' color='primary' />
                      <TimelineConnector />
                    </TimelineSeparator>
                    <TimelineContent>
                      <Typography variant='h5' gutterBottom>
                        {line}
                      </Typography>
                    </TimelineContent>
                  </TimelineItem>
                ))}
              </React.Fragment>
            </Timeline>
          </>
        )}
        <Toolbar sx={{ '& > :not(style)': { m: 1 }, justifyContent: 'center' }}>
          <Fab
            disabled={error != null}
            color='primary'
            aria-label='refresh'
            sx={{
              backgroundColor: 'white',
              color: 'black',
            }}
            variant='extended'
            onClick={() => refetch()}
          >
            <RefreshIcon />
          </Fab>
        </Toolbar>
      </CardContent>
    </Card>
  );
}
