import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';
import { DEVNET_HOSTNAME } from '../../app.config';

export interface IRxdWorkout {
  id: string;
  commands: string[];
  timestamp?: number;
  format: string;
}

export const api = createApi({
  baseQuery: fetchBaseQuery({ baseUrl: `${DEVNET_HOSTNAME}/api` }),
  tagTypes: ['rxd'],
  endpoints: (build) => ({
    getRxd: build.query<IRxdWorkout, string>({
      query: (format) => `beta/rxd?format=${format}`,
    }),
    getRxdById: build.query<IRxdWorkout, string>({
      query: (id) => `beta/rxd/${id}`,
    }),
  }),
});

export const { useGetRxdQuery,useGetRxdByIdQuery } = api;
